'use server';

import { prisma } from '@/lib/prisma';
import { revalidatePath } from 'next/cache';
import { writeFile, mkdir } from 'fs/promises';
import path from 'path';

export async function getPackages() {
  try {
    return await prisma.package.findMany({
      where: { isActive: true },
      orderBy: [{ sortOrder: 'asc' }, { createdAt: 'asc' }],
    });
  } catch (error) {
    console.error('Failed to fetch packages:', error);
    return [];
  }
}

export async function getAdminPackages() {
  try {
    return await prisma.package.findMany({
      orderBy: [{ sortOrder: 'asc' }, { createdAt: 'asc' }],
      include: { _count: { select: { orders: true } } },
    });
  } catch (error) {
    console.error('Failed to fetch admin packages:', error);
    return [];
  }
}

export async function getPackageById(id: string) {
  try {
    return await prisma.package.findUnique({ where: { id } });
  } catch {
    return null;
  }
}

export async function createPackage(data: {
  name: string;
  description?: string;
  price: string;
  category: string;
  imageUrl?: string;
  stock: number;
  isActive: boolean;
  sortOrder: number;
}) {
  try {
    await prisma.package.create({ data });
    revalidatePath('/');
    revalidatePath('/admin/packages');
    return { success: true };
  } catch (error) {
    console.error('Failed to create package:', error);
    return { success: false, error: 'Gagal membuat paket.' };
  }
}

export async function updatePackage(
  id: string,
  data: {
    name?: string;
    description?: string;
    price?: string;
    category?: string;
    imageUrl?: string;
    stock?: number;
    isActive?: boolean;
    sortOrder?: number;
  }
) {
  try {
    await prisma.package.update({ where: { id }, data });
    revalidatePath('/');
    revalidatePath('/admin/packages');
    return { success: true };
  } catch (error) {
    console.error('Failed to update package:', error);
    return { success: false, error: 'Gagal mengupdate paket.' };
  }
}

export async function deletePackage(id: string) {
  try {
    await prisma.package.delete({ where: { id } });
    revalidatePath('/');
    revalidatePath('/admin/packages');
    return { success: true };
  } catch (error) {
    console.error('Failed to delete package:', error);
    return { success: false, error: 'Gagal menghapus paket. Mungkin masih ada order terkait.' };
  }
}

export async function uploadPackageImage(formData: FormData): Promise<{ success: boolean; url?: string; error?: string }> {
  try {
    const file = formData.get('file') as File;
    if (!file || file.size === 0) return { success: false, error: 'File tidak ditemukan.' };

    const allowed = ['image/jpeg', 'image/png', 'image/webp', 'image/gif'];
    if (!allowed.includes(file.type)) return { success: false, error: 'Format tidak didukung. Gunakan JPG, PNG, atau WebP.' };

    if (file.size > 5 * 1024 * 1024) return { success: false, error: 'Ukuran file maksimal 5MB.' };

    const ext = file.name.split('.').pop();
    const filename = `pkg_${Date.now()}.${ext}`;
    const uploadDir = path.join(process.cwd(), 'public', 'uploads');
    await mkdir(uploadDir, { recursive: true });

    const buffer = Buffer.from(await file.arrayBuffer());
    await writeFile(path.join(uploadDir, filename), buffer);

    return { success: true, url: `/uploads/${filename}` };
  } catch (error) {
    console.error('Upload failed:', error);
    return { success: false, error: 'Upload gagal.' };
  }
}
